@use "../../assets/styles/color" as color;


.container{
    display: flex;
    justify-content: center;
    }

.mainContainer{
    display: flex;

}
.middleContainer{
    top: 0;
    width:639px;
    border-left: 1px solid #E6ECF0;
    border-right: 1px solid #E6ECF0;

}
.header{
    border-bottom: 1px solid #E6ECF0;
}
.followTable{
    width: 639px;
    border-bottom: 1px solid #E6ECF0;
}
ul{
    display: flex;
}

.followLinkActive {
    font-family: Noto Sans TC;
    font-size: 15px;
    font-weight: 700;
    line-height: 22px;
    width: 130px;
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 2px solid color.$brand-color;
    color: color.$brand-color;
}

.followLink{
    font-family: Noto Sans TC;
    font-size: 15px;
    font-weight: 700;
    line-height: 22px;
    width: 130px;
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    color:#657786;
    ;
    &:hover {
        font-size: medium;
        color:color.$brand-color;
    }
}
.isActive {
    color:color.$brand-color;
    border-bottom: 2px solid color.$brand-color;
  }
